  display: flex;
  margin-right: 15px;
}
.c-filter .clear {
  background: none;
  border: none;
  position: absolute;
  top: 0;
  right: 0;
  height: 36px;
  width: 36px;
  transition: 0.3s;
  opacity: .5;
  font-size: 26px;
  line-height: 1;
  display: none;
}
.c-filter .clear.is-mark {
  display: block;
}
.c-filter .clear:hover {
  opacity: 1;
}
.c-filter .form-group {
  position: relative;
  margin-bottom: 0;
  margin-right: 5px;
  width: 100%;
  max-width: 300px;
}
.c-filter .form-control {
  padding-right: 36px;
}
.c-schedule {
  overflow: hidden;
  position: relative;
  border: 2px solid #2196f3;
  display: inline-block;
  max-width: 100%;
  text-align: left;
  line-height: 1.2;
}
.c-schedule--lesson .c-schedule__table {
  position: relative;
  user-select: none;
  cursor: move;
}
.c-schedule--lesson .c-schedule__table .thead {
  background-color: #2196f3;
  color: #fff;
}
.c-schedule--lesson .c-schedule__table .thead .td {
  width: 500px;
  text-align: center;
  font-weight: 700;
  height: 32px;
}
.c-schedule--lesson .c-schedule__table .thead .lesson {
  width: 55px;
}
.c-schedule--lesson .c-schedule__table .tbody .td {
  border-top: 2px solid #ccc;
  border-right: 2px solid #ccc;
}
.c-schedule--lesson .c-schedule__table .tbody .td.lesson {
  border: none !important;
}
.c-schedule--lesson .c-schedule__table .tbody .td:last-child {
  border-right: none;
}
.c-schedule--lesson .c-schedule__table .tbody .tr:not(.day):hover {
  background-color: #eeeeee;
}
.c-schedule--lesson .c-schedule__table .tbody .tr:first-child .td {
  border-top: none;
}
.c-schedule--lesson .c-schedule__table .tbody .multiple .tr:first-child .td {
  border-top: 2px solid #ccc;
}
.c-schedule--lesson .c-schedule__table .tbody .multiple .tr:not(:first-child) .td {
  border-top: 1px solid #ccc;
}
.c-schedule--lesson .c-schedule__table .tbody .multiple:hover .tr {
  background-color: #eeeeee;
}
.c-schedule--lesson .c-schedule__table .tr {
  display: flex;
  transition: 0.3s;
  position: relative;
}
.c-schedule--lesson .c-schedule__table .td {
  flex-shrink: 0;
  padding: 5px;
}
.c-schedule--lesson .c-schedule__table .lesson {
  width: 55px;
  text-align: center;
  color: #fff;
  background-color: #2196f3;
}
.c-schedule--lesson .c-schedule__table .lesson:before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: -1px;
  bottom: 0;
  width: 1px;
  background-color: #2196f3;
}
.c-schedule--lesson .c-schedule__table .week {
  width: 75px;
  text-align: center;
}
.c-schedule--lesson .c-schedule__table .title {
  width: 300px;
}
.c-schedule--lesson .c-schedule__table .name {
  width: 150px;
}
.c-schedule--lesson .c-schedule__table .classroom {
  width: 50px;
  text-align: center;
}
.c-schedule--lesson .c-schedule__table .classroom:not(:last-child) {
  border-right-color: #2196f3;
}
.c-schedule--lesson .c-schedule__table .day {
  border-top: 2px solid #2196f3;
  border-bottom: 2px solid #2196f3;
}
.c-schedule--lesson .c-schedule__table .day .td {
  border: none;
  flex-grow: 0;
}
.c-schedule--lesson .c-schedule__table .day + .tr .td,
.c-schedule--lesson .c-schedule__table .day + .multiple .tr:first-child .td {
  border-top: none !important;
}
.c-schedule--lesson .c-schedule__head {
  overflow: hidden;
  z-index: 1;
}
.c-schedule--session {
  border-top: none;
}
.c-schedule--session .c-schedule__table {
  position: relative;
  user-select: none;
  cursor: move;
}
.c-schedule--session .c-schedule__head {
  border-top: 2px solid #2196f3;
  border-bottom: 2px solid #2196f3;
  overflow: hidden;
  z-index: 1;
  background-color: #fff;
}
.c-schedule--session .tr {
  display: flex;
  transition: 0.3s;
  position: relative;
}
.c-schedule--session .tr:last-child .td {
  border-bottom: none;
}
.c-schedule--session .tr:last-child .tr:not(:last-child) .td {
  border-bottom: 2px solid #ccc;
}
.c-schedule--session .tbody .tr:hover .td {
  background-color: #eeeeee;
}
.c-schedule--session .td {
  flex-shrink: 0;
  padding: 5px;
  border-bottom: 2px solid #ccc;
  border-right: 2px solid #ccc;
  text-align: center;
  background-color: #fff;
}
.c-schedule--session .td.is-grow {
  flex-grow: 1;
}
.c-schedule--session .td.is-fixed {
  position: relative;
  z-index: 1;
}
.c-schedule--session .td.is-fixed.td-5 {
  border-right-color: #2196f3;
}
.c-schedule--session .td:last-child {
  border-right: none;
}
