.c-footer {
  background-color: #2196f3;
  padding: 15px 0;
  margin-top: 15px;
  font-size: 14px;
  color: #fff;
  text-align: center;
}
.c-footer .item {
  display: inline-block;
}
.c-footer .item:not(:last-child) {
  margin-right: 15px;
}
.c-footer .link {
  color: #fff;
  will-change: opacity;
}
.c-footer .link:hover {
  opacity: .7;
}
.c-footer .o-social .link:hover {
  opacity: 1;
}
.c-footer .copyright {
  display: flex;
  flex-direction: column;
}
.c-footer .copyright .created {
  margin-top: 5px;
}
.c-footer .created .link {
  font-weight: 700;
}
@media (min-width: 640px) {
  .c-footer {
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
  }
}
@media (min-width: 768px) {
  .c-footer {
    text-align: left;
  }
  .c-footer .inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
  }
  .c-footer .list {
    order: 1;
  }
  .c-footer .o-social {
    margin-left: 30px;
  }
  .c-footer .copyright {
    flex-direction: row;
    justify-content: space-between;
    order: 1;
    width: 100%;
  }
  .c-copy {
    margin-left: 0;
    margin-right: auto;
  }
}
